<template>
<div align='center'>
    <div class="inline" ><router-link to="/"><button>Home</button></router-link></div>    
    <div class="inline" >
    <button @click="reset" id='reset'></button>
    <router-link to="/editor"><button>Editor</button></router-link>
    </div>    
    <h1></h1>
    <p/>
    <h2></h2>    
    <form id="upload">
        <input type="file" @change="change"/> 
        <input type="button" value="" :disabled="disabled" @click="load"/> 
    </form>
    <p/>    
    <p/>
    <p/>    
    <h2></h2>
    <p><a href="https://developer.mozilla.org/zh-CN/docs/Web/HTTP/Access_control_CORS"></a>
    <form id="upload">
        <input type="text" v-model="url" :size="size" @keyup.enter="load_from_url"/> 
        <input type="button" value="" @click="load_from_url"/> 
    </form>
</div>
</template>

<script>
var file = null;

export default {
  data: function() {
    return {
      disabled: true,
      url: ""
    };
  },
  computed: {
    size: function() {
      if (this.url.length + 1 < "".length) {
        return "".length;
      }
      return this.url.length + 1;
    }
  },
  methods: {
    reset: function() {
      var confir = confirm("");
      // fx58undefined..
      console.log("", confir);
      if (confir === true) {
        this.store.reset();
        alert("");
      } else if (confir === false) {
      } else {
        alert("Confirm()Bool, ");
      }
    },
    change: function(event) {
      try {
        if (event.target.files.length == 1) {
          this.disabled = false;
        }
        console.log(event.target.files[0]);
        file = event.target.files[0];
      } catch (error) {
        this.disabled = true;
        console.log("event.target.files.length: ", error);
      }
    },
    load: function() {
      console.log("load()");
      var reader = new FileReader();
      var self = this;
      reader.onload = function(event) {
        try {
          var tmpJson = JSON.parse(event.target.result);
          self.store.update(
            tmpJson.sites_tr_length,
            tmpJson.sites,
            tmpJson.search_engines
          );
          self.store.save_config_to_localStorage();
          alert("");
        } catch (error) {
          console.log(event.target.result);
          console.log(error);
          alert(" JSON");
        }
      };
      reader.onerror = function() {
        console.log(error);
        alert("");
      };
      reader.readAsText(file);
    },
    load_from_url: function() {
      console.log("load_from_url()");
      var self = this;
      this.http
        .get(this.url)
        .then(function(resp) {
          self.store.update(
            resp.data.sites_tr_length,
            resp.data.sites,
            resp.data.search_engines
          );
          self.store.save_config_to_localStorage();
          alert("");
        })
        .catch(function(error) {
          alert(": ", error.message);
          console.log(self.url);
          console.log(error);
        });
    }
  }
};
</script>

<style scoped>
.inline {
  width: 49%;
  display: inline-block;
}
#reset {
  margin: auto 20% auto auto;
  color: red;
}
</style>
